            // domecharts
            let _this = this;
            _this.from_now = echarts.init(document.getElementById('form_pic'));
            // 
            _this.from_now.setOption({
                title: {
                    text: '',
                    show: false,
                },
                tooltip: {},
                dataZoom: [{
                    type: 'slider',
                    start: _this.start_rate,
                    end: _this.end_rate
                }],
                legend: {
                    data: _this.legend_data,
                    z: 3
                },
                xAxis: {
                    name: _this.$t('forChanelTrade.bodyBank.lineX'),
                    data: _this.form_x_data.slice(0, _this.data_len),
                },
                yAxis: {
                    name: _this.$t('forChanelTrade.bodyBank.lineY')+_this.$store.state.currency,
                    type: 'value',
                    boundaryGap: ['0%', '5%'],
                },
                series: [{
                    name: _this.legend_data[0],
                    type: 'line',
                    smooth: true,
                    smoothMonotone: 'x',
                    data: _this.form_y_data.slice(0, _this.data_len),
