        //----
        rate_area(now_rate) {
            let _this = this;
            if (now_rate - 25 <= 0) {
                _this.start_rate = 0;
                _this.end_rate = 50;
            } else if (now_rate + 25 >= 100) {
                _this.start_rate = 50;
                _this.end_rate = 100;
            } else {
                _this.start_rate = now_rate - 25;
                _this.end_rate = now_rate + 25;
            }
        },
        //--
        change_date(date) {
            setTimeout(() => {
                this.get_formdata();
            }, 0);
        },
        //yyyy-MM-dd
        time_change(date) {
            let Year = date.getFullYear();
            let Month = this.time_long(date.getMonth() + 1);
            let day = this.time_long(date.getDate());
            return Year + '-' + Month + '-' + day;
        },
        // --001
        time_long(n) {
            if (n < 10) {
                return '0' + n;
            } else {
                return n;
            }
        },
