<template>
  <div>
    <div class="right_head">
      
    </div>
    <div class="right_body">
      <el-form label-width="120px" class="demo-ruleForm" :model="searchkey" :rules="searchkey_rule" ref="searchkey">
        <el-row :gutter="10">
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="" prop="name">
              <el-input v-model.trim="searchkey.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="" prop="city">
              <el-input v-model.trim="searchkey.city"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="">
              <el-select v-model.number="searchkey.type" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="busytype in busytypes_search.records" v-bind:value="busytype.tp" v-bind:label="busytype.text">
                </el-option>
              </el-select>
              <!-- <el-select v-model="searchkey.type" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
                <el-option label="" value="5"></el-option>
                <el-option label="" value="6"></el-option>
                <el-option label="" value="7"></el-option>
              </el-select> -->
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="">
              <el-select v-model="searchkey.openness" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="24" :lg="24">
            <div class="clearfix"></div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="">
              <el-select v-model="searchkey.audit_status" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="0"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="">
              <el-select v-model="searchkey.status" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="">
              <el-date-picker v-model="searchkey.s_join_dtm" type="datetime" align="right" placeholder="" :picker-options="pickerOptions_s" format="yyyy-MM-dd HH:mm:ss" @change="start_change" :editable="false" :clearable="false">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="">
              <el-date-picker v-model="searchkey.e_join_dtm" type="datetime" align="right" placeholder="" :picker-options="pickerOptions_e" format="yyyy-MM-dd HH:mm:ss" popper-class="no_now" :editable="false" :clearable="false">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label-width="0" class="t_c">
              <el-button type="primary" @click="search_sub_ajax"></el-button>
              <el-button type="default" icon="plus" @click="add_circle"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <template>
        <el-table :data="trade_now" border stripe style="width: 100%">
          <el-table-column prop="name" label="" resizable min-width="100px">
          </el-table-column>
          <el-table-column prop="type_str" label="" resizable min-width="120px">
          </el-table-column>
          <el-table-column label="" resizable min-width="100px">
            <template scope="scope">
              {{scope.row.province}}-{{scope.row.city}}
            </template>
          </el-table-column>
          <el-table-column label="" resizable min-width="120px">
            <template scope="scope">
              <span v-if="scope.row.openness==1"></span>
              <span v-else-if="scope.row.openness==2"></span>
              <span v-else></span>
            </template>
          </el-table-column>
          <el-table-column label="" resizable min-width="120px">
            <template scope="scope">
              <span v-if="scope.row.audit_status==1"></span>
              <span v-else-if="scope.row.audit_status==2"></span>
              <span v-else></span>
            </template>
          </el-table-column>
          <el-table-column label="" resizable min-width="120px">
            <template scope="scope">
              <span v-if="scope.row.status==0"></span>
              <span v-else></span>
            </template>
          </el-table-column>
          <el-table-column prop="jointime" label="" resizable min-width="170px">
          </el-table-column>
          <el-table-column label="" resizable min-width="160px">
            <template scope="scope">
              <div class="btn_margin">
                <el-button type="info" @click="scan_info(scope.row)"></el-button>
                <el-button type="success" @click="edit_info(scope.row)"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <div class="block t_r mar_t20">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_now" :page-sizes="[20,50,100,200,250]" :page-size="page_per" layout="total, sizes, prev, pager, next" :total="pages_all">
        </el-pagination>
      </div>
    </div>
    <load :visible="loading"></load>
    <load :visible="loading1"></load>
    <load :visible="loading2"></load>
    <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
    <bounced :visible="add_bounced" :newclass="big_bounced">
      <span slot="header">
            <template v-if="bounced_status==1"></template><template v-else-if="bounced_status==2"></template><template v-else></template>
            <i class="close"  @click="add_bounced=false"></i>
            </span>
      <el-form :model="add_info" :rules="add_info_rules" ref="add_info" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="add_info.name" type="text" auto-complete="off" @blur="if_right" :readonly="bounced_status==2"></el-input>
        </el-form-item>
        <el-form-item label="" prop="province">
          <el-select v-model="add_info.province" placeholder="" v-on:change="select_pro" filterable :disabled="bounced_status==2">
            <el-option v-for="province in pro_city" v-bind:areaid="province.areaid" v-bind:value="province.areaname" v-bind:label="province.areaname">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="city">
          <el-select v-model="add_info.city" placeholder="" v-on:change="select_city" filterable :disabled="bounced_status==2">
            <el-option v-for="city in citys" v-bind:cityid="city.cityid" v-bind:value="city.cityname" v-bind:label="city.cityname">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model.number="add_info.type" placeholder="" :disabled="bounced_status==2">
            <el-option v-for="busytype in busytypes.records" v-bind:value="busytype.tp" v-bind:label="busytype.text">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="openness">
          <el-select v-model="add_info.openness" placeholder="" :disabled="bounced_status==2">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" v-show="bounced_status!=2">
          <span class="bounced_button bounced_sure" v-bind:class="{bounced_sure_right:if_right_class}" @click="add_circle_sub"></span>
          <span class="bounced_button bounced_cancle" @click="add_bounced=false"></span>
        </el-form-item>
      </el-form>
    </bounced>
  </div>
</template>
<script>
export default {
  name: 'merchant_index',
  data() {
    return {
      loading: false, //load
      loading1: false, //load
      loading2: false, //load
      toastmsg: '', //toast
      visible_toast: false, //toast
      searchkey: {
        'name': '',
        'city': '',
        'type': '', //
        'openness': '',
        'audit_status': '',
        'status': '', //
        's_join_dtm': '',
        'e_join_dtm': '',
      },
      list_url: '/qudao/v1/api/region/list', //  
      add_url: '/qudao/v1/api/region/create', //  
      fix_url: '/qudao/v1/api/region/edit', //  
      qd_areacities_url: '/qudao/v1/api/tools/areacities', //   
      busy_style_url: '/qudao/v1/api/tools/regiontypes', //  
      busytypes_search: {},
      busytypes: {},
      pages_all: 0, //
      pages: 1, //
      page_per: 20, //
      page_now: 1, //
      trade_now: [], //
      now_year: new Date().getFullYear(), //
      now_month: new Date().getMonth() + 1, //
      end_day: new Date(), //
      start_day: this.get_daystart(), //
      pickerOptions_s: {
        disabledDate: (time) => time.getTime() > Date.now()
      },
      pickerOptions_e: {
        disabledDate: (time) => time.getTime() > this.end_day || time.getTime() < this.start_day
      },
      searchkey_rule: {
        name: [{
          required: false,
          pattern: /^[\u4e00-\u9fa5a-zA-Z0-9\s]{1,10}$/,
          message: ' 1  10 ',
          trigger: 'blur'
        }],
        city: [{
          required: false,
          pattern: /^[\u4e00-\u9fa5a-zA-Z0-9\s]{1,30}$/,
          message: ' 1  30 ',
          trigger: 'blur'
        }],
      },
      //---------------------------------------------------------------------------------------
      add_bounced: false, //
      big_bounced: 'big_bounced', //--
      bounced_status: 1, //1 2 3 
      now_id: '', //id
      if_right_class: false, //  
      pro_city: [], //ajax
      citys: [],
      reset_province: '',
      reset_city: '',
      add_info: {
        "name": "",
        "type": 1,
        "openness": "0",
        // "provinceid": '', // 
        "province": '', //
        // "cityid": '', // 
        "city": '', //
      },
      add_info_rules: {
        name: [{
          required: true,
          pattern: /^[\u4e00-\u9fa5a-zA-Z0-9\s]{1,10}$/,
          message: ' 1  10 ',
          trigger: 'blur'
        }],
        type: [{
          require: true,
          message: '',
          type: 'number',
          trigger: 'change',
        }],
        openness: [{
          require: true,
          message: '',
          trigger: 'change',
        }],
        province: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
        city: [{
          required: true,
          message: '',
          trigger: 'change'
        }],
      },
      if_city: false, //
      if_busystyle: false, //
    }
  },
  created: function() {
    this.get_area(); // 
    this.get_busytypes(); //
    //--
    //
    this.searchkey.s_join_dtm = this.get_daystart();
  },
  watch: {
    if_city: function(val, oldVal) {
      if (this.if_city && this.if_busystyle) {
        this.get_list();
      }
    }, //-
    if_busystyle: function(val, oldVal) {
      if (this.if_city && this.if_busystyle) {
        this.get_list();
      }
    }, //-
  },
  methods: {
    //--  
    if_right: function() {
      let _this = this;
      this.$refs.add_info.validate((valid) => {
        if (valid) {
          _this.if_right_class = true;
        } else {
          _this.if_right_class = false;
        }
      });
    },
    //toast
    onVisibleChange(val) {
      this.visible_toast = val; //
    },
    //toast
    onMsgChange(val) {
      this.toastmsg = val; //
    },
    //
    handleSizeChange(val) {
      let _this = this;
      _this.page_per = val;
      _this.page_now = 1;
      this.get_list();
    },
    //
    handleCurrentChange(val) {
      let _this = this;
      _this.page_now = val;
      this.get_list();
    },
    //
    get_list: function() {
      let _this = this;
      if (_this.searchkey.s_join_dtm.getTime() > _this.searchkey.e_join_dtm.getTime()) {
        _this.loading = false;
        _this.visible_toast = true;
        _this.toastmsg = '!';
        return false;
      }
      let post_data = {
        'page': _this.page_now,
        'page_size': _this.page_per,
        'name': _this.searchkey.name.replace(/\s/g, ''),
        'city': _this.searchkey.city.replace(/\s/g, ''),
        // 'type': _this.searchkey.type,
        // 'openness': _this.searchkey.openness - 0,
        // 'audit_status': _this.searchkey.audit_status - 0,
        // 'status': _this.searchkey.status - 0,
        's_join_dtm': _this.get_datetime(_this.searchkey.s_join_dtm),
        'e_join_dtm': _this.get_datetime(_this.searchkey.e_join_dtm),
      };
      if(this.searchkey.type.length>0){
        post_data.type = this.searchkey.type;
      }
      if(this.searchkey.openness.length>0){
        post_data.openness = this.searchkey.openness - 0;
      }
      if(this.searchkey.audit_status.length>0){
        post_data.audit_status = this.searchkey.audit_status - 0;
      }
      if(this.searchkey.status.length>0){
        post_data.status = this.searchkey.status - 0;
      }
      this.$ajax_log.ajax_get(this, this.list_url, post_data, (data_return) => {
        _this.pages_all = data_return.data.region_cnt;
        _this.trade_now = data_return.data.region_infos;
      });
    },
    //--
    search_sub_ajax: function() {
      this.$refs.searchkey.validate((valid) => {
        if (valid) {
          this.page_now = 1;
          this.get_list();
        } else {
          return false;
        }
      });
    },
    //
    add_circle: function() {
      this.$refs['add_info'].resetFields();//
      this.bounced_status = 1; //
      this.if_right_class = false; //
      //
      this.add_info.name = ''; //
      this.add_info.openness = '0'; //
      this.add_info.type = 1; //
      this.add_info.province = this.reset_province; //
      setTimeout(() => {
        this.add_info.city = this.reset_city; //
      }, 0);
      this.add_bounced = true; //
    },
    //
    scan_info: function(val) {
      this.$refs['add_info'].resetFields();//
      // console.log(val);
      this.bounced_status = 2; //
      //
      this.add_info.name = val.name; //
      this.add_info.openness = val.openness.toString(); //
      this.add_info.type = val.type - 0; //
      this.add_info.province = val.province; //
      setTimeout(() => {
        this.add_info.city = val.city; //
      }, 100);
      this.add_bounced = true; //
    },
    //
    edit_info: function(val) {
      this.$refs['add_info'].resetFields();//
      // console.log(val);
      this.bounced_status = 3; //
      //
      this.add_info.name = val.name; //
      this.add_info.openness = val.openness.toString(); //
      this.add_info.type = val.type - 0; //
      this.add_info.province = val.province; //
      setTimeout(() => {
        this.add_info.city = val.city; //
      }, 100);
      this.now_id = val.id;
      this.add_bounced = true; //
    },
    //
    add_circle_sub: function() {
      let _this = this;
      this.$refs.add_info.validate((valid) => {
        if (valid) {
          if (_this.if_right_class) {
            _this.add_sub();
          }
        } else {
          return false;
        }
      });
    },
    //or
    add_sub: function() {
      let _this = this;
      let url_now;
      // let post_data = {};
      // post_data = this.add_info;
      let post_data = {
        'name': this.add_info.name,
        'province': this.add_info.province,
        'city': this.add_info.city,
        'type': this.add_info.type,
        'openness': this.add_info.openness - 0,
      };
      if (this.bounced_status == 1) {
        url_now = this.add_url;
        // post_data.id = '';
      } else if (this.bounced_status == 3) {
        url_now = this.fix_url;
        post_data.id = this.now_id;
      }
      this.$ajax_log.ajax_post(this, url_now, post_data, (data_return) => {
        _this.add_bounced = false; //
        _this.get_list(); //
      });
    },
    //----------------------start-------------------------------------------------------------------------------------
    //
    get_area: function() {
      var _this = this;
      this.$ajax_log.ajax_load_get(this, 'loading1', this.qd_areacities_url, { 'no_whole_country': 1 }, (data_return) => {
        _this.pro_city = data_return.data.records;
        //
        _this.citys = _this.pro_city[0].cities;
        _this.reset_province = _this.pro_city[0].areaname;
        _this.reset_city = _this.citys[0].cityname;
        //
        _this.add_info.province = _this.pro_city[0].areaname;
        _this.add_info.city = _this.citys[0].cityname;
        _this.if_city = true;
      });
    },
    //
    select_pro: function(val) {
      for (let i = 0; i < this.pro_city.length; i++) {
        if (this.pro_city[i].areaname == this.add_info.province) {
          // this.add_info.provinceid = this.pro_city[i].areaid;
          this.citys = this.pro_city[i].cities;
          //console.log('');
          //
          // if (!this.if_reset_branchbank) {
          //   //console.log('1');
          //   return false; //falsetrue
          // }
          setTimeout(() => {
            //console.log('2');
            this.add_info.city = this.citys[0].cityname;
            // this.add_info.cityid = this.citys[0].cityid; 
          }, 0);
          return false;
        } else {
          this.citys = [];
        }
      }
    },
    //id
    select_city: function(val) {
      for (let i = 0; i < this.citys.length; i++) {
        if (this.citys[i].cityname == this.add_info.city) {
          // this.add_info.cityid = this.citys[i].cityid;
          return false;
        }
      }
    },
    //----------------------end-------------------------------------------------------------------------------------
    //
    get_busytypes: function() {
      var _this = this;
      this.$ajax_log.ajax_load_get(this, 'loading2', this.busy_style_url, '', (data_return) => {
        _this.busytypes = data_return.data;
        _this.busytypes_search = data_return.data;
        _this.if_busystyle = true;
      });
    },
    //----------------------start-------------------------------------------------------------------------------------
    // 
    start_change(time) {
      let _this = this;
      let iosTime = time.replace(/-/g, '/');
      let date = new Date(iosTime);
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      //--
      let end_t = this.searchkey.e_join_dtm;
      let end_t_y = new Date(end_t).getFullYear();
      let end_t_m = new Date(end_t).getMonth() + 1;
      let end_t_d = new Date(end_t).getDate();
      //,
      let day = date.getDate();
      let now_day = year + '/' + month + '/' + day + ' 00:00:00';
      _this.start_day = new Date(now_day);
      //<
      let endTime = new Date(_this.searchkey.e_join_dtm).getTime();
      if (endTime - _this.start_day.getTime() < 0) {
        _this.searchkey.e_join_dtm = new Date();
      }
    },
    //0
    get_daystart: function() {
      let _this = this;
      let d = new Date();
      let year = d.getFullYear();
      let month = _this.date_long(d.getMonth() + 1);
      let day = _this.date_long(d.getDate());
      //
      let ddend = year + '/' + month + '/' + day + ' 23:59:59';
      this.end_day = new Date(ddend);
      setTimeout(() => {
        this.searchkey.e_join_dtm = new Date(ddend);
      }, 0);
      //
      let b_year = year,
        b_dd = '';
      let b_month = month - 5;
      if (b_month <= 0) {
        b_year = year - 1;
        b_month = 12 + b_month;
      }
      b_dd = b_year + '/' + this.date_long(b_month) + '/' + '01 00:00:00';
      return new Date(b_dd);
    },
    //
    get_datetime: function(d) {
      let _this = this;
      let date = new Date(d);
      let year = date.getFullYear();
      let month = _this.date_long(date.getMonth() + 1);
      let day = _this.date_long(date.getDate());
      let hour = _this.date_long(date.getHours());
      let min = _this.date_long(date.getMinutes());
      let sec = _this.date_long(date.getSeconds());
      return year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + sec;
    },
    //101---
    date_long: function(n) {
      if (n < 10) {
        return '0' + n;
      } else {
        return n;
      }
    },
    //----------------------end-------------------------------------------------------------------------------------
  },
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped rel="stylesheet/less">
.btn_margin .el-button {
  margin: 5px 0;
}

</style>



