// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router' //

import routes from './route/' //
import store from './store/' //
import filter from './filter/' //
import component from './components/' //

import VueResource from 'vue-resource' //ajax
//import 'element-theme-default'; //
import '../theme/index.css'; //--#ff8100--  node_modules/.bin/et -i    node_modules/.bin/et
import ElementUI from 'element-ui' //

//mock-----------------------------------------------------------------------------------------------
//import '../mock/page_app.js'

//---------------------------------------------------------------------------------------------
//import './monitor'
//ajax---------------------------------------------------------------------------------------------
import ajax_log from './method/ajax_log'
Vue.prototype.$ajax_log = ajax_log; //ajax


//---------------------------------------------------------------------------------------------
import VueI18n from 'vue-i18n'
import ElementLocale from 'element-ui/lib/locale'
import messages from './duoyu/' //
Vue.use(VueI18n);
//cookie
import cookie from './method/cookie' //cookie
//--localStorage 
//( language)localStorage.lang
//store--
//js language 
//cookielang
let langNow;
let language_lan = navigator.browserLanguage?navigator.browserLanguage:navigator.language; //
let language = language_lan.split('-')[0];
if(language!=='zh' && language!=='en' ){
  language='zh';
}
if (localStorage.lang && localStorage.lang!="undefined") {
  cookie.SetCookie('lang', localStorage.lang, 1);//cookielang
  langNow = localStorage.lang;
  store.commit('t_language',localStorage.lang);//language
} else {
  localStorage.lang = language;//storage
  cookie.SetCookie('lang', language, 1);//cookielang
  store.commit('t_language',language);//language
  langNow = language;//
}
// Create VueI18n instance with options
const i18n = new VueI18n({
  locale: langNow, // set locale
  messages, // set locale messages
});
ElementLocale.i18n((key, value) => i18n.t(key, value)); //element

Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(ElementUI);
const router = new VueRouter({
    routes //  routes: routes
});
//
router.beforeEach(({ meta, path }, from, next) => {
    var { auth = true } = meta
    var isLogin = Boolean(store.state.sessionid) //true false
        // console.log(auth);
        // console.log(store.state.sessionid);
        // console.log(isLogin);
    if (path == '/login') {
        store.commit('login_ify');
    } else {
        store.commit('login_ifn');
    }
    if (auth && !isLogin && path !== '/login') {
        store.commit('login_ify');
        return next({ path: '/login' })
    }
    next()
});
//Vue.http.options.emulateJSON = true;//contenttypeapplication/x-www-form-urlencodedapplication/json
Vue.http.interceptors.push((request, next) => {
  next((response) => {
    if (response.body.respcd == 1001) {
      //store.commit('t_login_no', true);
      //window.location.href = location.protocol + '//' + location.host + '/qudao/v1/page/login.html';
      router.replace({
          path: '/login'
      });
      return false
    } else {
      return response
    }
  });
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router,
  store,
  i18n
})



