<template>
    <div>
        <div class="right_head">
            
        </div>
        <div class="right_body">
            <el-form label-width="110px" class="demo-ruleForm form_width" ref="new_notice" :model="new_notice" :rules="notice_rule">
                <el-form-item label="" prop="title">
                    <el-input v-model="new_notice.title" type="text" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="content">
                    <el-input v-model="new_notice.content" type="textarea" :rows="6" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="qd_list">
                    <el-input v-model="new_notice.qd_list" type="textarea" :rows="6" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="new_notice.istop" on-text="" off-text=""></el-switch>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('new_notice')"></el-button>
                    <el-button @click="resetForm('new_notice')"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <load :visible="loading"></load>
        <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
        <bounced :visible="opratedialog">
            <p class="bounced_text">
                
            </p>
            <!-- <input type="hidden" name="" v-model="notice_id"> -->
            <p slot="footer">
                <span class="bounced_button bounced_sure" @click="mend_chan"></span>
                <span class="bounced_button bounced_cancle" @click="opratedialog = false"></span>
            </p>
        </bounced>
    </div>
</template>
<script>
export default {
    name: 'addNotice',
    data() {
        return {
            loading: false, //load
            toastmsg: '', //toast
            visible_toast: false, //toast
            opratedialog: false, //
            notice_url: location.protocol + '//' + location.host + '/qudao/v1/api/announcement/create', // 
            new_notice: {
                "title": '',
                "content": '',
                "qd_list": '',
                "istop": false,
            }, //
            notice_rule: {
                title: [{
                    required: true,
                    //pattern: /^[\u4e00-\u9fa5]{1,4}$/,
                    message: '',
                    trigger: 'blur'
                }, {
                    min: 1,
                    max: 50,
                    message: '50',
                    trigger: 'blur'
                }],
                content: [{
                    required: true,
                    //pattern: /^[\u4e00-\u9fa5]{1,4}$/,
                    message: '',
                    trigger: 'blur'
                }, {
                    min: 1,
                    max: 1000,
                    message: '1000',
                    trigger: 'blur'
                }],
                qd_list: [{
                    required: false,
                    //pattern: /^[\d+\,{1}(?=\d+)]{0,2000}\d+$/,
                    pattern: /^[\d+\s\,{1}(?=\d+\s)]{1,2000}$/,
                    message: 'id',
                    trigger: 'blur'
                }],
            },
        }
    },
    created: function() {},
    watch: {},
    methods: {
        //toast
        onVisibleChange(val) {
            this.visible_toast = val; //
        },
        //toast
        onMsgChange(val) {
            this.toastmsg = val; //
        },
        submitForm(formName) {
            let _this = this;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    //_this.sure_ajax();
                    _this.opratedialog=true;//
                } else {
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        //
        mend_chan(){
            this.opratedialog=false;
            this.sure_ajax();
        },
        sure_ajax: function() {
            let _this = this;
            if(this.new_notice.istop){
                this.new_notice.istop=1;
            }else{
                this.new_notice.istop=0;
            }
            //console.log(_this.new_notice);
            _this.new_notice.qd_list=_this.new_notice.qd_list.replace(/\s/g,'');
            this.$http.post(this.notice_url, _this.new_notice, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    let data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.visible_toast = true;
                        _this.toastmsg = '!';
                        _this.$refs.new_notice.resetFields();//
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
    },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less" rel="stylesheet/less">
.el-form.form_width {
    width: 80%;
    margin: 0 auto;
    .el-button {
        width: 45%;
    }
}
</style>



