import yanzheng from '../../method/yanzheng'
export default {
  name: 's_channel',
  data() {
    return {
      loading: false, //load
      toastmsg: '', //toast
      visible_toast: false, //toast
      searchkey: {
        'mchnt_userid': '',
        'mchnt_name': '',
        'start_time': '',
        'end_time': '',
      },
      list_url: location.protocol + '//' + location.host + '/qudao/v1/api/settlement/qd_royalty/list', //      
      down_url: location.protocol + '//' + location.host + '/qudao/v1/api/settlement/qd_royalty/list/download', //                  
      pages_all: 0, //
      pages: 1, //
      page_per: 10, //
      page_now: 1, //
      table_data: [], //
      now_year: new Date().getFullYear(), //
      now_month: new Date().getMonth() + 1, //
      last_day: new Date(), //
      start_day: this.get_daystart(), //
      pickerOptions_s: {
        disabledDate: (time) => time.getTime() > Date.now()
      },
      pickerOptions_e: {
        disabledDate: (time) => time.getTime() > this.last_day || time.getTime() < this.start_day
      },
      searchkey_rule: {
        mchnt_userid: [{
          required: false,
          pattern: /^[0-9\s]{0,300}$/,
          message: '',
          trigger: 'blur'
        }],
        mchnt_name: yanzheng.test_qd_name(this.$t('generalPro.bodyBank.qdName'), 1, 30, false, 'blur', this),
      },
    }
  },
  created: function() {
    //
    this.searchkey.start_time = this.get_daystart();
    this.searchkey.end_time = new Date();
    setTimeout(() => {
      this.get_list(); //
    }, 0);
  },
  methods: {
    //toast
    onVisibleChange(val) {
      this.visible_toast = val; //
    },
    //toast
    onMsgChange(val) {
      this.toastmsg = val; //
    },
    //
    get_list: function() {
      let _this = this;
      if (_this.searchkey.start_time.getTime() > _this.searchkey.end_time.getTime()) {
        _this.loading = false;
        _this.visible_toast = true;
        _this.toastmsg = '!';
        return false;
      }
      let post_data = {
        'page': _this.page_now,
        'page_size': _this.page_per,
        'qd_uid': _this.searchkey.mchnt_userid.replace(/\s/g, ''),
        'qd_name': _this.searchkey.mchnt_name.replace(/\s/g, ''),
        'start_date': _this.get_datetime(_this.searchkey.start_time),
        'end_date': _this.get_datetime(_this.searchkey.end_time),
      };
      //console.log(post_data);
      this.$http.get(this.list_url, {
          'params': post_data,
          before: function() {
            _this.loading = true;
          }
        })
        .then(function(response) {
          _this.loading = false;
          let data_return = response.body;
          if (data_return.respcd == '0000') {
            _this.pages_all = data_return.data.total_cnt;
            _this.table_data = data_return.data.records;
          } else {
            if (data_return.respmsg) {
              _this.toastmsg = data_return.respmsg;
            } else {
              _this.toastmsg = data_return.resperr;
            }
            _this.visible_toast = true;
          }
        }, function(response) {
          _this.loading = false;
          _this.visible_toast = true;
          _this.toastmsg = '!';
        })
        .catch(function(response) {
          _this.loading = false;
        });
      //--
      //_this.getdata_test();
    },
    //
    handleSizeChange(val) {
      let _this = this;
      _this.page_per = val;
      _this.page_now = 1;
      this.get_list();
    },
    //
    handleCurrentChange(val) {
      let _this = this;
      _this.page_now = val;
      this.get_list();
    },
    //--
    search_sub_ajax: function() {
      this.$refs.searchkey.validate((valid) => {
        if (valid) {
          this.get_list();
        } else {
          return false;
        }
      });
      // this.get_list();
    },
    //
    start_change(time) {
      let _this = this;
      let iosTime = time.replace(/-/g,'/');
      let date = new Date(iosTime);
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      //
      let now_day = year + '/' + month + '/' + '01' + ' 00:00:00';
      _this.start_day = new Date(now_day);
    },
    //
    download(data_val) {
      let _this = this;
      let post_data = {
        'qd_uid': data_val.qd_uid,
        'qd_name': data_val.qd_name,
        // 'start_date': _this.get_datetime(_this.searchkey.start_time),
        // 'end_date': _this.get_datetime(_this.searchkey.end_time),
        'start_date': data_val.trade_date,
        'end_date': data_val.trade_date,
      };
      let url = this.down_url + '?';
      let data;
      for (data in post_data) {
        url += data + '=' + post_data[data] + '&';
      }
      url = url.substr(0, url.length - 1);
      window.open(url);
    },
    //11--
    get_daystart: function() {
      let _this = this;
      let d = new Date();
      let year = d.getFullYear();
      // let month = _this.date_long(d.getMonth() + 1);
      // let day = _this.date_long(d.getDate());
      // let dd = year + '/' + month + '/' + day + ' 00:00:00';
      let dd = year + '/' + '01' + '/' + '01';
      return new Date(dd);
    },
    //--ajax
    get_datetime: function(d) {
      let _this = this;
      let date = new Date(d);
      let year = date.getFullYear();
      let month = _this.date_long(date.getMonth() + 1);
      let day = _this.date_long(date.getDate());
      let hour = _this.date_long(date.getHours());
      let min = _this.date_long(date.getMinutes());
      let sec = _this.date_long(date.getSeconds());
      //return year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + sec;
      // return year + '-' + month + '-' + day;
      return year + '-' + month;
    },
    //101
    date_long: function(n) {
      if (n < 10) {
        return '0' + n;
      } else {
        return n;
      }
    },
