                        <el-option label="-" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="weight">
                    <el-input v-model="add_ader.weight" type="text" auto-complete="off" @blur="if_right"></el-input>
                </el-form-item>
                <el-form-item label="" prop="status">
                    <el-select v-model="add_ader.status" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="memo">
                    <el-input type="textarea" :rows="3" placeholder="" v-model="add_ader.memo" @blur="if_right">
                    </el-input>
                </el-form-item>
                <el-form-item label="">
