<script>
export default {
    name: 's_channel',
    data() {
        return {
            loading: false, //load
            toastmsg: '', //toast
            visible_toast: false, //toast
            searchkey: {
                'name': '',
                'status': '',
                'during_date': '',
            },
            // list_url: this.$store.state.ad_url + '/adv/banner/list', // 
            // adder_url: this.$store.state.ad_url + '/adv/banner/create?format=cors', //   
            // adder_fix_url: this.$store.state.ad_url + '/adv/banner/edit?format=cors', //   
            // adder_to_url: this.$store.state.ad_url + '/adv/redirect/create?format=cors', //  
            list_url: '/qudao/adv/banner/list', // 
            adder_url: '/qudao/adv/banner/create', //   
            adder_fix_url: '/qudao/adv/banner/edit', //   
            adder_to_url: '/qudao/adv/redirect/create', //                                
            pages_all: 0, //
            pages: 1, //
            page_per: 10, //
            page_now: 1, //
            table_data: [], //
            pickerOptions: {
                // disabledDate(time) {
                //     return time.getTime() >= Date.now();
                // }
            },
            searchkey_rule: {
                name: [{
                    required: false,
                    // pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]{1,30}$/,
                    // message: ' 1  30 (//)',
                    message: ' 1  30 ',
                    min: 0,
                    max: 30,
                    trigger: 'blur'
                }],
                status: [{
                    require: false,
                    message: '',
                    trigger: 'change',
                }],
            },
            add_ad: false, //
            big_bounced: 'big_bounced mar_t_exp', //--
            add_or_fix: true, //or,true 
            if_right_class: false, //  
            if_clickSure: false,//---if_right_class
            now_id: '', //id
            target_url: '', //
            add_ader: {
                "name": "",
                "origin_url": "",
                "end_time": "",
                "start_time": "",
                "status": "1",
                "memo": "",
                "daterange": "",
                "weight": "",
                "display_page": "1",
                "image_url": "",
            },
            add_ader_rules: {
                name: [{
                    required: true,
                    // pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]{1,30}$/,
                    // message: ' 1  30 (//)',
                    message: ' 1  30 ',
                    min: 0,
                    max: 30,
                    trigger: 'blur'
                }],
                origin_url: [{
                    required: true,
                    pattern: /^(http)/,
                    message: ' 0  500 ',
                    min: 0,
                    max: 500,
                    trigger: 'blur'
                }],
                image_url: [{
                    required: false,
                    pattern: /^(http)/,
                    message: ' 0  500 ',
                    min: 0,
                    max: 500,
                    trigger: 'blur'
                }],
                end_time: [{
                    required: false,
                    message: '',
                    trigger: 'blur'
                }],
                start_time: [{
                    required: false,
                    message: '',
                    trigger: 'blur'
                }],
                status: [{
                    required: false,
                    message: '',
                    trigger: 'change',
                }],
                memo: [{
                    required: false,
                    message: ' 0  200 ',
                    min: 0,
                    max: 200,
                    trigger: 'blur'
                }],
                display_page: [{
                    required: true,
                    message: '',
                    trigger: 'change',
                }],
                weight: [{
                    required: true,
                    pattern: /^[0-9]{1,100000000000}$/,
                    message: '',
                    trigger: 'blur'
                }],
                // daterange: [{
                //     type: 'date',
                //     required: true,
                //     message: '',
                //     trigger: 'change'
                // }],
            },
            pickerOptions_erea: {
                disabledDate: (time) => time.getTime() < Date.now() - 3600 * 1000 * 24,
                shortcuts: [{
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        end.setTime(end.getTime() + 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        end.setTime(end.getTime() + 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        end.setTime(end.getTime() + 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            },
        }
    },
    created: function() {
        //
        this.searchkey.during_date = new Date();
        setTimeout(() => {
            this.get_list(); //
        }, 0);
    },
    methods: {
        //--  
        if_right: function() {
            let _this = this;
            this.$refs.add_ader.validate((valid) => {
                if (valid) {
                    _this.if_right_class = true;
                } else {
                    _this.if_right_class = false;
                }
            });
        },
        //toast
        onVisibleChange(val) {
            this.visible_toast = val; //
        },
        //toast
        onMsgChange(val) {
            this.toastmsg = val; //
        },
        //
        get_list: function() {
            let _this = this;
            let post_data = {
                'start': (_this.page_now - 1) * this.page_per,
                'length': _this.page_per,
                'name': _this.searchkey.name,
                'status': _this.searchkey.status,
                'during_date': _this.searchkey.during_date.toString().indexOf('-') != '-1' ? this.searchkey.during_date : this.time_change_dearch(this.searchkey.during_date),
                //"format": 'cors',
            };
            //console.log(post_data);
            this.$http.get(this.list_url, {
                    'params': post_data,
                    //credentials: true,
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    let data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.pages_all = data_return.data.count;
                        _this.table_data = data_return.data.list;
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            //--
            //_this.getdata_test();
        },
        //
        handleSizeChange(val) {
            let _this = this;
            _this.page_per = val;
            _this.page_now = 1;
            this.get_list();
        },
        //
        handleCurrentChange(val) {
            let _this = this;
            _this.page_now = val;
            this.get_list();
        },
        //--
        search_sub_ajax: function() {
            let _this = this;
            this.$refs.searchkey.validate((valid) => {
                if (valid) {
                    _this.get_list();
                } else {
                    return false;
                }
            });
        },
        //
        resetForm(formName) {
            this.$refs[formName].resetFields();
            //
            this.searchkey.during_date = new Date();
        },
        //--
        change_date(date) {
            this.searchkey.during_date = date;
        },
        //--
        time_change(val) {
            //console.log(val);
            if (!val) {
                this.add_ader.start_time = '';
                this.add_ader.end_time = '';
            } else {
                let timerange = val.split(' - ');
                this.add_ader.start_time = timerange[0] + ' 00:00:00';
                this.add_ader.end_time = timerange[1] + ' 23:59:59';
            }
            //console.log(this.add_ader.start_time);
        },
        //
        add_open: function() {
            this.if_clickSure = false;
            this.add_or_fix = true; //
            this.$refs.add_ader.resetFields(); //
            this.if_right_class = false; //--
            //this.add_ader.daterange = ''; //
            this.get_daterange(); //
            this.add_ad = true; //
        },
        //
        fix_open: function(val) {
            this.if_clickSure = false;
            this.add_or_fix = false; //
            this.$refs.add_ader.resetFields(); //
            this.if_right_class = true; //--
            this.add_ader.daterange = ''; //
            this.add_ader = {
                "name": val.name,
                "origin_url": val.origin_url,
                "image_url": val.image_url,
                "end_time": val.end_time,
                "start_time": val.start_time,
                "status": val.status.toString(),
                "memo": val.memo,
                "daterange": [this.get_datetime(val.start_time), this.get_datetime(val.end_time)],
                "weight": val.weight,
                "display_page": val.display_page.toString(),
            }; //
            this.now_id = val.id; //id
            this.add_ad = true; //
        },
        //
        add_ader_sub: function() {
            this.if_clickSure = true;
            let _this = this;
            this.$refs.add_ader.validate((valid) => {
                if (valid) {
                    //_this.to_sub();
                    _this.adder_sub();
                } else {
                    return false;
                }
            });
        },
        //
        to_sub: function() {
            let _this = this;
            _this.add_ad = false; //
            let post_data = {};
            post_data = {
                "name": this.add_ader.name,
                "status": this.add_ader.status,
                "origin_url": this.add_ader.origin_url,
                //"ownerid":"",
            };
            this.$http.post(this.adder_to_url, post_data, {
                    emulateJSON: true,
                    //credentials: true,
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.target_url = data_return.data.target_url; //
                        _this.adder_sub();
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
        //or
        adder_sub: function() {
            let _this = this;
            //_this.add_ad = false; //
            let url_now;
            let post_data = {
                "name": this.add_ader.name,
                //"target_url": this.target_url,
                "origin_url": this.add_ader.origin_url,
                "image_url": this.add_ader.image_url,
                "end_time": this.add_ader.end_time,
                "start_time": this.add_ader.start_time,
                "status": this.add_ader.status,
                "memo": this.add_ader.memo,
                "weight": this.add_ader.weight,
                "display_page": this.add_ader.display_page,
            };
            if (this.add_or_fix) {
                url_now = this.adder_url;
            } else {
                url_now = this.adder_fix_url;
                post_data.id = this.now_id;
            }
            this.$http.post(url_now, post_data, {
                    emulateJSON: true,
                    //credentials: true,
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.get_list(); //
                        _this.add_ad = false; //
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
        //--
        get_daterange: function() {
            let d = new Date();
            let year = d.getFullYear();
            let month = d.getMonth();
            let day = d.getDate();
            //this.add_ader.daterange = [new Date(year, month, 1), d]; //-
            this.add_ader.daterange = [d, new Date(d.getTime() + 3600 * 1000 * 24 * 30)]; //--
        },
        //yyyy-MM-dd--
        time_change_dearch(date) {
            let Year = date.getFullYear();
            let Month = this.date_long(date.getMonth() + 1);
            let day = this.date_long(date.getDate());
            return Year + '-' + Month + '-' + day;
        },
        //
        get_datetime: function(d) {
            let time_zu = d.split('-');
            let date_str = time_zu[0] + '-' + time_zu[1] + '-' + time_zu[2];
            return new Date(date_str);
        },
        //101
        date_long: function(n) {
            if (n < 10) {
                return '0' + n;
            } else {
                return n;
            }
        },
