<template>
    <div>
        <div class="right_head">
            
        </div>
        <div class="right_body time_date_short">
            <el-form label-width="100px" class="demo-ruleForm" :model="searchkey" :rules="searchkey_rule" ref="searchkey">
                <el-row :gutter="10">
                    <el-col :xs="24" :sm="24" :md="12" :lg="8">
                        <el-form-item label="" prop="name">
                            <el-input v-model.trim="searchkey.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :xs="24" :sm="24" :md="12" :lg="8">
                        <el-form-item label="" prop="status">
                            <el-select v-model="searchkey.status" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="0"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :xs="24" :sm="24" :md="12" :lg="8">
                        <el-form-item label-width="0" class="t_c">
                            <el-button type="primary" @click="search_sub_ajax"></el-button>
                            <el-button type="default" @click="resetForm('searchkey')"></el-button>
                            <el-button type="primary" icon="plus" @click="add_open"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <template>
                <el-table :data="table_data" border stripe style="width: 100%">
                    <el-table-column prop="id" label="id" resizable min-width="120px">
                    </el-table-column>
                    <el-table-column prop="name" label="" resizable min-width="130px">
                    </el-table-column>
                    <el-table-column prop="contacts" label="" resizable min-width="120px">
                    </el-table-column>
                    <el-table-column prop="mobile" label="" resizable min-width="120px">
                    </el-table-column>
                    <el-table-column prop="memo" label="" resizable min-width="100px">
                        <template scope="scope">
                            <span v-if="scope.row.status"></span>
                            <span v-else></span>
                        </template>
                    </el-table-column>
                    <el-table-column label="" resizable min-width="100px">
                        <template scope="scope">
                            <el-button type="warning" icon="edit" @click="fix_open(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
            <div class="block t_r mar_t20">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_now" :page-sizes="[10,20,50,100,200,250]" :page-size="page_per" layout="total, sizes, prev, pager, next" :total="pages_all">
                </el-pagination>
            </div>
        </div>
        <load :visible="loading"></load>
        <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
        <bounced :visible="add_ad" :newclass="big_bounced">
            <span slot="header">
            <template v-if="add_or_fix"></template><template v-else></template>
            <i class="close"  @click="add_ad=false"></i>
            </span>
            <el-form :model="add_ader" :rules="add_ader_rules" ref="add_ader" label-width="100px" class="demo-ruleForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="add_ader.name" type="text" auto-complete="off"  @blur="if_right"></el-input>
                </el-form-item>
                <el-form-item label="" prop="addr">
                    <el-input v-model="add_ader.addr" type="text" auto-complete="off"  @blur="if_right"></el-input>
                </el-form-item>
                <el-form-item label="" prop="contacts">
                    <el-input v-model="add_ader.contacts" type="text" auto-complete="off"  @blur="if_right"></el-input>
                </el-form-item>
                <el-form-item label="" prop="mobile">
                    <el-input v-model="add_ader.mobile" type="text" auto-complete="off"  @blur="if_right"></el-input>
                </el-form-item>
                <el-form-item label="" prop="demo_href">
                    <el-input v-model="add_ader.demo_href" type="text" auto-complete="off"  @blur="if_right"></el-input>
                </el-form-item>
                <el-form-item label="" prop="status">
                    <el-select v-model="add_ader.status" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="memo">
                    <el-input type="textarea" :rows="3" placeholder="" v-model="add_ader.memo"  @blur="if_right">
                    </el-input>
                </el-form-item>
                <el-form-item label="">
                    <span class="bounced_button bounced_sure" v-bind:class="{bounced_sure_right:if_right_class}" @click="add_ader_sub"></span>
                    <span class="bounced_button bounced_cancle" @click="add_ad=false"></span>
                </el-form-item>
            </el-form>
        </bounced>
    </div>
</template>
<script>
export default {
    name: 's_merchant',
    data() {
        return {
            loading: false, //load
            toastmsg: '', //toast
            visible_toast: false, //toast
            searchkey: {
                'name': '',
                'status': '',
            },
            // list_url: this.$store.state.ad_url + '/adv/owner/list', // 
            // adder_url: this.$store.state.ad_url + '/adv/owner/create?format=cors', //   
            // adder_fix_url: this.$store.state.ad_url + '/adv/owner/edit?format=cors', // 
            list_url: '/qudao/adv/owner/list', // 
            adder_url: '/qudao/adv/owner/create', //   
            adder_fix_url: '/qudao/adv/owner/edit', //        
            pages_all: 0, //
            pages: 1, //
            page_per: 10, //
            page_now: 1, //
            table_data: [], //
            searchkey_rule: {
                name: [{
                    required: false,
                    message: ' 1  30 ',
                    min: 0,
                    max: 30,
                    trigger: 'blur'
                }],
                status: [{
                    require: false,
                    message: '',
                    trigger: 'change',
                }],
            },
            add_ad: false, //
            big_bounced: 'big_bounced mar_t_exp', //--
            add_or_fix: true, //or,true 
            now_id: '', //id
            if_right_class: false, //  
            add_ader: {
                "name": "",
                "addr": "",
                "contacts": "",
                "mobile": "",
                "demo_href": "",
                "status": "1",
                "memo": "",
            },
            add_ader_rules: {
                name: [{
                    required: true,
                    //pattern: /^[\u4e00-\u9fa5a-zA-Z0-9]{1,30}$/,
                    //message: ' 1  30 (//)',
                    message: ' 1  30 ',
                    min: 1,
                    max: 30,
                    trigger: 'blur'
                }],
                addr: [{
                    required: false,
                    message: ' 0  30 ',
                    min: 0,
                    max: 30,
                    //message: ' 0  30 ',
                    trigger: 'blur'
                }],
                contacts: [{
                    required: true,
                    message: ' 1  30 ',
                    min: 1,
                    max: 30,
                    trigger: 'blur'
                }],
                mobile: [{
                    required: true,
                    pattern: /(^(0\d{2,3}(-){0,1}){1}\d{7,8})(-\d{3,5}){0,1}$|^1[0-9]{10}$/,
                    message: '',
                    trigger: 'blur'
                }],
                demo_href: [{
                    required: false,
                    message: ' 0  200 ',
                    min: 0,
                    max: 200,
                    trigger: 'blur'
                }],
                status: [{
                    require: false,
                    message: '',
                    trigger: 'change',
                }],
                memo: [{
                    required: false,
                    message: ' 0  200 ',
                    min: 0,
                    max: 200,
                    trigger: 'blur'
                }],
            },
        }
    },
    created: function() {
        setTimeout(() => {
            this.get_list(); //
        }, 0);
    },
    methods: {
        //--  
        if_right: function() {
            let _this = this;
            this.$refs.add_ader.validate((valid) => {
                if (valid) {
                    _this.if_right_class = true;
                } else {
                    _this.if_right_class = false;
                }
            });
        },
        //toast
        onVisibleChange(val) {
            this.visible_toast = val; //
        },
        //toast
        onMsgChange(val) {
            this.toastmsg = val; //
        },
        //
        get_list: function() {
            let _this = this;
            let post_data = {
                'start': (_this.page_now - 1) * this.page_per,
                'length': _this.page_per,
                'name': _this.searchkey.name,
                'status': _this.searchkey.status,
                //"format": 'cors',
            };
            //console.log(post_data);
            this.$http.get(this.list_url, {
                    'params': post_data,
                    //'credentials': true,
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    let data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.pages_all = data_return.data.count;
                        _this.table_data = data_return.data.list;
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            //--
            //_this.getdata_test();
        },
        //
        handleSizeChange(val) {
            let _this = this;
            _this.page_per = val;
            _this.page_now = 1;
            this.get_list();
        },
        //
        handleCurrentChange(val) {
            let _this = this;
            _this.page_now = val;
            this.get_list();
        },
        //--
        search_sub_ajax: function() {
            let _this = this;
            this.$refs.searchkey.validate((valid) => {
                if (valid) {
                    _this.get_list();
                } else {
                    return false;
                }
            });
        },
        //
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        //
        add_open: function() {
            this.add_or_fix = true; //
            this.$refs.add_ader.resetFields(); //
            this.if_right_class=false;//--
            this.add_ad = true; //
        },
        //
        fix_open: function(val) {
            //console.log(val);
            this.add_or_fix = false; //
            this.$refs.add_ader.resetFields(); //
            this.if_right_class=true;//--
            this.add_ader = {
                "name": val.name ? val.name : '',
                "addr": val.addr ? val.addr : '',
                "contacts": val.contacts ? val.contacts : '',
                "mobile": val.mobile ? val.mobile : '',
                "demo_href": val.demo_href ? val.demo_href : '',
                "status": val.status.toString(),
                "memo": val.memo ? val.memo : '',
            };//
            this.now_id = val.id; //id
            this.add_ad = true; //
        },
        //
        add_ader_sub: function() {
            let _this = this;
            this.$refs.add_ader.validate((valid) => {
                if (valid) {
                    _this.adder_sub();
                } else {
                    return false;
                }
            });
        },
        //or
        adder_sub: function() {
            let _this = this;
            _this.add_ad = false; //
            let url_now;
            let post_data={};
            post_data = this.add_ader;
            if (this.add_or_fix) {
                url_now = this.adder_url;
                post_data.id = '';
            } else {
                url_now = this.adder_fix_url;
                post_data.id = this.now_id;
            }
            this.$http.post(url_now, post_data, {
                    'emulateJSON': true, //application/x-www-form-urlencoded
                    //'credentials': true, //cookie
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.get_list(); //
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
        //--
        getdata_test() {
            let _this = this;
            _this.trades = [{
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 0,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 1,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 0,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 1,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 0,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 1,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 0,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 1,
            }, {
                "ctime": "2017-03-01", // 
                "id": 11111115,
                "name": "",
                "contacts": "",
                "mobile": "",
                "memo": "",
                "status": 1,
            }];
            _this.table_data = _this.trades;
            _this.pages_all = 100;
        },
    },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less">
.bounced_my .bounced_my_body.big_bounced.mar_t_exp {
    //top: 10%;
}
</style>



