<script>
import yanzheng from '../../method/yanzheng'
import util from '../../method/util'
export default {
  name: 'channel_index',
  data() {
    var checkamt = (rule, value, callback) => {
      // if (!value) {
      //     return callback(new Error(''));
      // }
      //let reg = /^[0-9]{0,9}$/;
      let reg = /^[0-9]{0,9}(\.{1}\d{1,2}){0,1}$/;
      //value = value.toString().replace(/\D/g, '');
      setTimeout(() => {
        if (!reg.test(value)) {
          callback(new Error(',9'));
        } else {
          callback();
        }
      }, 100);
    };
    var check_fee_has = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }
      let reg = /^[0-9]{0,2}(\.{1}\d{1,2}){0,1}$/;
      //value = value.toString().replace(/\D/g, '');
      setTimeout(() => {
        if (!reg.test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      }, 100);
    };
    var check_fee = (rule, value, callback) => {
      let reg = /^[0-9]{0,2}(\.{1}\d{1,2}){0,1}$/;
      //value = value.toString().replace(/\D/g, '');
      setTimeout(() => {
        if (!reg.test(value)) {
          callback(new Error(''));
        } else if (value > 0 && value < 0.38) {
          callback(new Error('0.38% 0.38'));
        } else {
          callback();
        }
      }, 100);
    };
