  .step_show_inner {
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }
  p.title {
    font-size: 16px;
    margin-bottom: 22px;
    padding-left: 20px;
    width: 100%;
    position: relative;
    box-sizing: border-box;
    &:before {
      content: '';
      width: 3px;
      height: 14px;
      position: absolute;
      left: 0;
      background: @orange;
      top: 2px;
    }
  }
  .el-select {
    width: 100%;
  }
}

.big_pic {
  width: 100%;
  padding: 25px 165px;
  box-sizing: border-box;
  text-align: center;
  img {
    width: 100%;
    height: auto;
  }
}

.add_quyu {
  margin: 0 0 15px 50px;
}

</style>



