<template>
  <div>
    <div class="right_head">
      
    </div>
    <div class="right_body">
      <el-form label-width="110px" class="demo-ruleForm" :model="searchkey" :rules="searchkey_rule" ref="searchkey">
        <el-row :gutter="10">
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label="" prop="name">
              <el-input v-model.trim="searchkey.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="6">
            <el-form-item label-width="0" class="t_c">
              <el-button type="primary" @click="search_sub"></el-button>
              <!-- <el-button type="default" @click="publicDialog=true"></el-button> -->
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <template>
        <el-table :data="material_now" border stripe style="width: 100%">
          <el-table-column prop="name" label="" resizable min-width="120px">
          </el-table-column>
          <el-table-column prop="create_time" label="" resizable min-width="170px">
          </el-table-column>
          <el-table-column label="" resizable min-width="100px">
            <template scope="scope">
              <el-button type="info" @click="scanDetail(scope.row)"></el-button>
              <!-- <el-button type="warning" @click="delMaterial(scope.row)"></el-button> -->
            </template>
          </el-table-column>
        </el-table>
      </template>
      <div class="block t_r mar_t20">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_now" :page-sizes="[20,50,100,200,250]" :page-size="page_per" layout="total, sizes, prev, pager, next" :total="pages_all">
        </el-pagination>
      </div>
    </div>
    <load :visible="loading"></load>
    <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
    <bounced :visible="opratedialog">
      <p class="bounced_text">
        <span v-if="if_del"></span><span v-else></span>
      </p>
      <p slot="footer">
        <span class="bounced_button bounced_sure" @click="mend_chan"></span>
        <span class="bounced_button bounced_cancle" @click="opratedialog = false"></span>
      </p>
    </bounced>
    <bounced :visible="publicDialog" :newclass="large_bounced">
      <span slot="header"><i class="close"  @click="publicDialog = false"></i></span>
      <el-form label-width="110px" class="demo-ruleForm form_width" ref="new_material" :model="new_material" :rules="material_rule">
        <el-form-item label="" prop="name">
          <el-input v-model="new_material.name" type="text" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="link">
          <el-input v-model="new_material.link" type="text" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="memo">
          <el-input v-model="new_material.memo" type="textarea" :rows="6" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('new_material')"></el-button>
          <el-button @click="resetForm('new_material')"></el-button>
        </el-form-item>
      </el-form>
    </bounced>
    <bounced :visible="detaiDialog" :newclass="large_bounced">
      <span slot="header"><i class="close"  @click="detaiDialog = false"></i></span>
      <el-form label-width="110px" class="demo-ruleForm form_width" ref="scan_material" :model="scan_material">
        <el-form-item label="">{{scan_material.name}}
        </el-form-item>
        <el-form-item label=""><a :href="scan_material.link" target="_blank">{{scan_material.link}}</a>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scan_material.memo" type="textarea" :rows="6" auto-complete="off"  :readonly="true"></el-input>
        </el-form-item>
      </el-form>
    </bounced>
  </div>
</template>
<script>
import yanzheng from '../../method/yanzheng'
export default {
  name: 'material',
  data() {
    return {
      loading: false, //load
      toastmsg: '', //toast
      visible_toast: false, //toast
      opratedialog: false, //
      publicDialog: false, //
      detaiDialog: false, //
      large_bounced: 'large_bounced', //
      searchkey: {
        name: '',
      },
      searchkey_rule: {
        name: yanzheng.test_any('',1,30,false,'blur'),
      },
      material_now: [], //
      pages_all: 0, //
      page_per: 20, //
      page_now: 1, //
      pages: 1, //
      oprate_data: {}, //
      del_id: '', //id
      if_del: false, //     
      list_url: location.protocol + '//' + location.host + '/qudao/v1/api/training_file/list', //  
      material_url: location.protocol + '//' + location.host + '/qudao/v1/api/announcement/create', // --
      materialDel_url: location.protocol + '//' + location.host + '/qudao/v1/api/announcement/create', // --
      new_material: {
        "name": '',
        "memo": '',
        "link": '',
      }, //
      scan_material: {
        "name": '',
        "memo": '',
        "link": '',
      }, //
      material_rule: {
        name: yanzheng.test_any('',1,30,true,'blur'),
        memo: yanzheng.test_any('',1,500,true,'blur'),
        link: [{
          required: true,
          //pattern: /^http(s)?:\/\//,
          message: '',
          trigger: 'blur'
        }],
      },
    }
  },
  created: function() {
    this.get_list(); // 
  },
  methods: {
    //toast
    onVisibleChange(val) {
      this.visible_toast = val; //
    },
    //toast
    onMsgChange(val) {
      this.toastmsg = val; //
    },
    handleSizeChange(val) {
      let _this = this;
      //console.log(` ${val} `);
      _this.page_per = val;
      _this.page_now = 1;
      this.get_list();
    },
    handleCurrentChange(val) {
      //console.log(`: ${val}`);
      let _this = this;
      _this.page_now = val;
      this.get_list();
    },
    //
    get_list: function() {
      let _this = this;
      let post_data = {
        'page': _this.page_now,
        'page_size': _this.page_per,
        'name': _this.searchkey.name.replace(/\s/g, ''),
      };
      this.$http.get(this.list_url, {
          'params': post_data,
          before: function() {
            _this.loading = true;
          }
        })
        .then(function(response) {
          _this.loading = false;
          let data_return = response.body;
          if (data_return.respcd == '0000') {
            _this.pages_all = data_return.data.total_cnt;
            _this.material_now = data_return.data.records;
          } else {
            if (data_return.respmsg) {
              _this.toastmsg = data_return.respmsg;
            } else {
              _this.toastmsg = data_return.resperr;
            }
            _this.visible_toast = true;
          }
        }, function(response) {
          _this.loading = false;
          _this.visible_toast = true;
          _this.toastmsg = '!';
        })
        .catch(function(response) {
          _this.loading = false;
        });
    },
    //
    search_sub: function() {
      this.$refs.searchkey.validate((valid) => {
        if (valid) {
          this.get_list();
        } else {
          return false;
        }
      });
    },
    //
    scanDetail: function(val) {
      this.scan_material = val;
      this.detaiDialog = true;
    },
    // --
    delMaterial: function(val) {
      this.del_id = val.id;
      this.if_del = true;
      this.opratedialog = true;
    },
    //-- --
    mend_chan() {
      this.opratedialog = false; //
      if (this.if_del) {
        this.sureDel_ajax();
      } else {
        this.sure_ajax();
      }
    },
    submitForm(formName) {
      let _this = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.if_del = false;
          this.publicDialog = false; //
          this.opratedialog = true; //
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // --
    sure_ajax: function() {
      let _this = this;
      this.$http.post(this.material_url, _this.new_material, {
          before: function() {
            _this.loading = true;
          }
        })
        .then(function(response) {
          _this.loading = false;
          let data_return = response.body;
          if (data_return.respcd == '0000') {
            _this.visible_toast = true;
            _this.toastmsg = '!';
            _this.$refs.new_material.resetFields(); //
            _this.get_list();//
          } else {
            if (data_return.respmsg) {
              _this.toastmsg = data_return.respmsg;
            } else {
              _this.toastmsg = data_return.resperr;
            }
            _this.visible_toast = true;
          }
        }, function(response) {
          _this.loading = false;
          _this.visible_toast = true;
          _this.toastmsg = '!';
        })
        .catch(function(response) {
          _this.loading = false;
        });
    },
    // --
    sureDel_ajax: function() {
      let _this = this;
      this.$http.post(this.materialDel_url, _this.new_material, {
          before: function() {
            _this.loading = true;
          }
        })
        .then(function(response) {
          _this.loading = false;
          let data_return = response.body;
          if (data_return.respcd == '0000') {
            _this.visible_toast = true;
            _this.toastmsg = '!';
            _this.get_list();//
          } else {
            if (data_return.respmsg) {
              _this.toastmsg = data_return.respmsg;
            } else {
              _this.toastmsg = data_return.resperr;
            }
            _this.visible_toast = true;
          }
        }, function(response) {
          _this.loading = false;
          _this.visible_toast = true;
          _this.toastmsg = '!';
        })
        .catch(function(response) {
          _this.loading = false;
        });
    },
  },
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less" rel="stylesheet/less">
.el-form.form_width {
  width: 80%;
  margin: 0 auto;
  .el-button {
    width: 45%;
  }
}

</style>



