<template>
    <div>
        <div class="right_head">
            
        </div>
        <div class="right_body">
            <template>
                <el-table :data="salesmen_now" border stripe style="width: 100%">
                    <el-table-column prop="title" label="" resizable min-width="360px">
                        <template scope="scope">
                            <span class="top_span" v-if="scope.row.istop"></span>
                            <span class="top_span" v-else></span>
                            <!-- <span class="top_span " v-if="scope.row.read"></span>
                            <span class="top_span red" v-else></span> -->
                            <el-button type="text" @click="open_dialog(scope.row)">{{scope.row.title}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column prop="create_time" label="" resizable min-width="120px">
                    </el-table-column>
                    <el-table-column label="" resizable min-width="120px">
                        <template scope="scope">                            
                            <template v-if="scope.row.can_modify"><el-button :plain="true" type="warning" @click="open_chan(scope.row)"></el-button></template>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
            <div class="block t_r mar_t20">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_now" :page-sizes="[10,20,50,100,200,250]" :page-size="page_per" layout="total, sizes, prev, pager, next" :total="pages_all">
                </el-pagination>
            </div>
        </div>
        <load :visible="loading"></load>
        <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
        <bounced :visible="dialogslsm" :newclass="big_bounced">
            <span slot="header"><i class="close"  @click="dialogslsm = false"></i></span>
            <el-form label-width="80px" class="demo-ruleForm">
                <el-form-item label="">
                    <el-input v-model="now_notice.title" type="text" auto-complete="off" :readonly="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="now_notice.content" type="textarea" :rows="6" auto-complete="off" :readonly="true"></el-input>
                </el-form-item>
            </el-form>
        </bounced>
        <bounced :visible="opratedialog">
            <p class="bounced_text">
                
            </p>
            <input type="hidden" name="" v-model="notice_id">
            <p slot="footer">
                <span class="bounced_button bounced_sure" @click="mend_chan"></span>
                <span class="bounced_button bounced_cancle" @click="opratedialog = false"></span>
            </p>
        </bounced>
    </div>
</template>
<script>
export default {
    name: 'notice',
    data() {
        return {
            loading: false, //load
            toastmsg: '', //toast
            visible_toast: false, //toast
            dialogslsm: false, // 
            big_bounced: 'big_bounced', //-- 
            opratedialog: false, //
            salesmen: [],
            slsm_url: location.protocol + '//' + location.host + '/qudao/v1/api/announcement/list', // 
            read_url: location.protocol + '//' + location.host + '/qudao/v1/api/announcement/', // 
            del_url: location.protocol + '//' + location.host + '/qudao/v1/api/announcement/', //--
            pages_all: 0, //
            page_per: 10, //
            page_now: 1, //
            pages: 1, //
            salesmen_mid: [], //
            salesmen_now: [], //
            now_notice: {
                "title": '',
                "content": '',
            }, //
            notice_id: '', //id
            if_read:'',//10
        }
    },
    created: function() {
        this.get_list(); //  
    },
    watch: {},
    methods: {
        //toast
        onVisibleChange(val) {
            this.visible_toast = val; //
        },
        //toast
        onMsgChange(val) {
            this.toastmsg = val; //
        },
        //
        get_list: function() {
            let _this = this;
            let post_data = {
                'page': _this.page_now,
                'page_size': _this.page_per,
                'read':'',
            };
            this.$http.get(this.slsm_url, {
                    'params': post_data,
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    let data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.pages_all = data_return.data.total_cnt;
                        _this.salesmen_now = data_return.data.records;
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            //--
            //_this.getdata_test();
        },
        //
        handleSizeChange(val) {
            let _this = this;
            //console.log(` ${val} `);
            _this.page_per = val;
            _this.page_now = 1;
            this.get_list();
        },
        //
        handleCurrentChange(val) {
            //console.log(`: ${val}`);
            let _this = this;
            _this.page_now = val;
            this.get_list();
        },
        //
        open_dialog(row) {
            this.now_notice = {
                "title": row.title,
                "content": row.content,
            };
            this.dialogslsm = true;
            // if(!row.read){
            //     this.get_read(row);
            //     row.read=1;//
            //     this.get_read_no();//
            //     //
