                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
        //--
        get_read_no() {
            let _this = this;
            let post_data = {
                'page': 0,
                'page_size': 20,
                'read':0,
            };
            this.$http.get(this.slsm_url, {
                    'params': post_data,
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    let data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.$store.commit('t_notice_read_no', data_return.data.total_cnt);//
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
        //
        open_chan: function(val) {
            this.notice_id = val.announce_id;
            this.if_read = val.read;
            this.opratedialog = true;
        },
        //
        mend_chan: function() {
            let _this = this;
            _this.opratedialog = false; //
            let post_data = {
                'status': 1,
            };
            this.$http.post(this.del_url + this.notice_id + '/status', post_data, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    let data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.get_list();
                        // if(!_this.if_read){
                        //     _this.get_read_no();//
                        // }                        
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
