            'mchnt_mchid': _this.searchkey.mchid.replace(/\s/g, ''),
            'audit_statuses': _this.searchkey.audit_statuses,
          };
          let url = this.down_url + '?';
          let data;
          for (data in post_data) {
            url += data + '=' + post_data[data] + '&';
          }
          url = url.substr(0, url.length - 1);
          window.open(url);
        } else {
          return false;
        }
      });
