<template>
  <div>
    <div class="right_head">
      
    </div>
    <div class="right_body">
      <template>
        <el-table :data="list_now" border stripe style="width: 100%">
          <el-table-column prop="fee_ratio_id" label="id" resizable min-width="150px">
          </el-table-column>
          <el-table-column label="" resizable min-width="150px">
            <template scope="scope">
              {{scope.row.fee_type==1?'':scope.row.fee_type==2?'':''}}
            </template>
          </el-table-column>
          <el-table-column label="" resizable min-width="100px">
            <template scope="scope">
              <span v-if="scope.row.payway_fee_mode!=2">{{scope.row.fee_percent}}%</span>               
            </template>
          </el-table-column>
          <el-table-column label="/" resizable min-width="120px">
            <template scope="scope">
              <span v-if="scope.row.payway_fee_mode!=1">{{(scope.row.fee_fix/100).toFixed(2)}}</span>              
            </template>
          </el-table-column>
          <el-table-column prop="fee_memo" label="" resizable min-width="180px">
          </el-table-column>
          <el-table-column prop="payway_id" label="id" resizable min-width="180px">
          </el-table-column>
          <el-table-column prop="payway_title" label="" resizable min-width="180px">
          </el-table-column>
          <el-table-column label="" resizable min-width="170px">
            <template scope="scope">
              {{scope.row.payway_fee_mode==1?'':scope.row.payway_fee_mode==2?'':''}}
            </template>
          </el-table-column>
          <el-table-column label="" resizable min-width="100px">
            <template scope="scope">
              <el-button type="text" @click="mendD(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <load :visible="loading"></load>
    <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
    <bounced :visible="opratedialog">
      <p class="bounced_text">
        <span></span>
      </p>
      <p slot="footer">
        <span class="bounced_button bounced_sure" @click="mend_chan"></span>
        <span class="bounced_button bounced_cancle" @click="opratedialog = false"></span>
      </p>
    </bounced>
    <bounced :visible="publicDialog" :newclass="large_bounced">
      <span slot="header"><!-- -{{mendTitle}} --><i class="close"  @click="publicDialog = false"></i></span>
      <el-form label-width="110px" class="demo-ruleForm form_width" ref="mend_form" :model="mend_form" :rules="mend_form_rule">
        <el-form-item label="">
          {{mend_form.fee_type==1?'':mend_form.fee_type==2?'':''}}
        </el-form-item>
        <el-form-item label="id">
          {{mend_form.fee_ratio_id}}
        </el-form-item>
        <el-form-item label="" prop="fee_percent" v-if="mend_form.payway_fee_mode==1 || mend_form.payway_fee_mode==3">
          <el-input v-model="mend_form.fee_percent">
            <template slot="append">%</template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="fee_fix" v-if="mend_form.payway_fee_mode==2 || mend_form.payway_fee_mode==3">
          <el-input v-model="mend_form.fee_fix">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="fee_memo">
          <el-input v-model="mend_form.fee_memo" type="textarea" :rows="4" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="id">
          {{mend_form.payway_id}}
        </el-form-item>
        <el-form-item label="">
          {{mend_form.payway_fee_mode==1?'':mend_form.payway_fee_mode==2?'':''}}
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('mend_form')"></el-button>
        </el-form-item>
      </el-form>
    </bounced>
  </div>
</template>
<script>
import yanzheng from '../../method/yanzheng'
export default {
  name: 'merchant_index',
  data() {
    var check_fee_has = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }
      let reg = /^[0-9]{0,3}(\.{1}\d{1,2}){0,1}$/;
      //value = value.toString().replace(/\D/g, '');
      setTimeout(() => {
        if (!reg.test(value)) {
          callback(new Error(''));
        } else if (value - 0 > 100) {
          callback(new Error('100'));
        } else {
          callback();
        }
      }, 100);
    };
    return {
      loading: false, //load
      toastmsg: '', //toast
      visible_toast: false, //toast
      publicDialog: false, //
      opratedialog: false, //
      large_bounced: 'big_bounced',
      list_url: '/qudao/v1/api/payquick_ratio/list', //  
      mend_url: '/qudao/v1/api/payquick_ratio/modify',
      list_now: [], //
      mendTitle:'',//--
      mend_form: {
        'fee_type': '',
        'fee_ratio_id': '',
        'fee_percent': '',
        'fee_fix': '',
        'fee_memo': '',
        'payway_id': '',
        'payway_fee_mode': '',
      },
      mend_form_rule: {
        fee_memo: yanzheng.test_any('', 0, 500, false, 'blur'),
        fee_percent: [{
          validator: check_fee_has,
          required: true,
          trigger: 'blur'
        }],
        fee_fix: [{
          required: true,
          validator: yanzheng.checkamt_need,
          trigger: 'blur'
        }],
      },
    }
  },
  created: function() {
    this.get_list();
  },
  watch: {

  },
  methods: {
    //toast
    onVisibleChange(val) {
      this.visible_toast = val; //
    },
    //toast
    onMsgChange(val) {
      this.toastmsg = val; //
    },
    //
    get_list: function() {
      let _this = this;
      let post_data = {};
      this.$ajax_log.ajax_get(this, this.list_url, post_data, (data_return) => {
        _this.list_now = data_return.data.records;
      });
    },
    //
    mendD(val) {
      this.mend_form = {
        'fee_type': val.fee_type,
        'fee_ratio_id': val.fee_ratio_id,
        'fee_percent': val.fee_percent,
        'fee_fix': val.fee_fix / 100,
        'fee_memo': val.fee_memo,
        'payway_id': val.payway_id,
        'payway_fee_mode': val.payway_fee_mode,
      };
      this.mendTitle = val.payway_title;
      this.publicDialog = true; //
    },
    submitForm(formName) {
      let _this = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.publicDialog = false; //
          this.opratedialog = true; //
        } else {
          return false;
        }
      });
    },
    //--
    mend_chan() {
      this.opratedialog = false; //
      this.sure_ajax();
    },
    //
    sure_ajax: function() {
      let _this = this;
      this.mend_form.fee_fix = yanzheng.floatToint(this.mend_form.fee_fix);
      this.$ajax_log.ajax_post(this, this.mend_url, this.mend_form, (data_return) => {
        _this.visible_toast = true;
        _this.toastmsg = 'success!';
        _this.get_list();
        _this.$refs.mend_form.resetFields(); //
      }, (data_return) => {
        _this.$refs.mend_form.resetFields(); //
      }, (data_return) => {
        _this.$refs.mend_form.resetFields(); //
      });
    },
  },
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less">


</style>



